export const messageOf: (tag: string) => Observable<MessageEvent> =
    tag => message().pipe(
        filter((msg: MessageEvent) => <'setting' | 'browse'>msg.data.tag === tag)
    );

export const netStatusChange: () => Observable<string> =
    () => merge(online(), offline()).pipe(
        map((e: Event) => e.type)
    );
