import { fromEvent, Observable } from 'rxjs';
import { sampleTime, filter } from 'rxjs/operators';

// 

// 
// onload
// onbeforeunload
// onhashchange
// onpopstate
// onvisibilitychange
// ononline
// onoffline
// onmessage

